@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-style: normal;
  font-weight: 800;
}

:root {
  --color-border: #e1e7ed;
  --color-shadow: #3d4e611a;
  --color-shadow-hover: #00000040;
  --color-text: #3d4e61;
}

body {
  margin: 0;
  padding: 0;
}

.top__bar {
  flex-direction: column;
  margin: 20px 8px 0;
  display: flex;
}

.search__box {
  border: 1px solid var(--color-border);
  box-shadow: 0 1px 8px 0 var(--color-shadow);
  background: #fff;
  border-radius: 4px;
  align-items: center;
  display: flex;
}

.search__box:hover {
  box-shadow: 0 4px 4px 0 var(--color-shadow-hover);
}

.search {
  background: none;
  border: none;
  outline: none;
  width: 100%;
  padding: 0;
  font-family: Avenir, sans-serif;
  display: flex;
}

.search:focus {
  text-shadow: 1px 4px 4px var(--color-shadow-hover);
  color: var(--color-text);
  border-radius: 3px;
}

.search::placeholder {
  text-shadow: none;
  color: var(--color-text);
  font-family: Avenir, sans-serif;
  font-weight: 300;
  line-height: normal;
}

.search__glass {
  margin-bottom: 1px;
  display: flex;
}

.search__img-big {
  width: 19px;
  height: 19px;
  margin-left: 25px;
}

.search__img-small {
  width: 11px;
  height: 11px;
  margin-left: 13px;
}

.search--big {
  height: 68px;
  margin-left: 18px;
  font-size: 16px;
}

.search--small {
  height: 40px;
  margin-left: 9px;
  font-size: 14px;
}
